export function financeARClearance(state = initialState, action) {
    switch (action.type) {
        case LIST:
        case UPDATE_LIST:
        case SET_GENERAL:
            return {
                ...state,
                ...action.payload
            }
        case UPDATE_FIELD:
            return update(state, action.payload)
        default:
            return state;
    }
}