import { callApi } from 'Utils/apiUtil'
import { listUrl, updateListUrl } from 'Utils/urlUtil'

export const LIST = 'LIST';
export const UPDATE_LIST = 'UPDATE_LIST';
export const SET_GENERAL = 'SET_GENERAL';
export const UPDATE_FIELD = 'UPDATE_FIELD';

// Send To Backend Request customer list
export function getList(id) {
    return (dispatch, getState) => {
        const reducer = getState().financeARClearance
        const searchfields = reducer.searchfields
        const options = {
            method: 'POST',
        }
        
        options.body = JSON.stringify({
            queryKey: searchfields.queryKey.value,
            type: searchfields.type.value,
            payment: searchfields.payment.value,
            userId: id
        })

        /* const request = () => ({ type: LIST, payload: {  status: 'success' } })
        const success = (response) => {
            return { type: LIST, payload: { status: 'success' } }
        }
        const failure = () => ({ type: LIST, payload: { status: 'success' } }) */

        const request = () => ({ type: LIST, payload: { list: {}, listLoaded: false, status: 'saving' } })
        const success = (response) => {
            return { type: LIST, payload: { list: response, listLoaded: true, status: response.status === 'success' ? 'success' : 'failed' } }
        }
        const failure = () => ({ type: LIST, payload: { list: {}, listLoaded: true, status: 'failed' } })

        return dispatch(callApi(listUrl, options, request, success, failure))
    }
}

export function updateList(id,params) {
    const options = {
        method: 'post',
    }
    options.body = JSON.stringify({
        userId: id,
        ...params
    })

    const request = () => ({ type: UPDATE_LIST, payload: { savingStatus: 'saving' } })
    const success = (response) => ({ type: UPDATE_LIST, payload: { savingStatus: response.status } })
    const failure = () => ({ type: UPDATE_LIST, payload: { savingStatus: 'failed' } })
    
    return callApi(updateListUrl, options, request, success, failure)
}

export function setGeneral(params) {
    return {
        type: SET_GENERAL,
        payload: params
    }
}

export function updateSearchFields(params) {
    return {
        type: UPDATE_FIELD,
        payload: { searchfields: params }
    }
}